import commander from "commander";
import { unlinkSync } from "fs";
import { unitTest, createNycrcFile } from "./unitTest";
import { coveralls } from "./coveralls";

export function addUnitTestCommand() {
  commander
    .command("test")
    .option("--coveralls", "", false)
    .description("")
    .action(pars => {
      const projectPath = process.cwd();
      const nycrcFile = createNycrcFile(projectPath); // .nycrc
      unitTest(projectPath, []); // 
      if (pars.coveralls) {
        coveralls(projectPath, []); // 
      }
      unlinkSync(nycrcFile); // .nycrc
    });
}
