import commander from "commander";
import chalk from "chalk";
import { build } from "./build";
import { doc } from "./doc";

export function addBuildCommand() {
  commander
    .command("build")
    .option("--doc", "", false)
    .description("")
    .action(pars => {
      build()
        .then()
        .catch((e: Error) => {
          console.error(chalk.red(` ${e.message}`));
          process.exit(1);
        });

      if (pars.doc) {
        doc(process.cwd());
      }
    });
}
